<template>
  <div class="login-container">
    <!-- 별 애니메이션 -->
    <div class="star-field">
      <div
        class="star"
        v-for="n in 100"
        :key="n"
        :style="getRandomStarStyle()"
      ></div>
    </div>

    <div class="login-box">
      <h1 class="login-title">카카오 소셜 로그인</h1>
      <p class="login-description">카카오 계정으로 로그인하세요.</p>
      <!-- 카카오 로그인 이미지 버튼 -->
      <img
        :src="kakaoButtonImage"
        alt="Kakao Login"
        class="login-button"
        @click="handleKakaoLogin"
        :class="{ disabled: loading }"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "SignIn",
  data() {
    return {
      loading: false,
      kakaoButtonImage: require("@/assets/kakaologin.png"), // 이미지 경로 설정
      userName: null, // 사용자 이름
      errorMessage: null, // 오류 메시지
    };
  },
  mounted() {
    // 카카오 리다이렉트로 전달된 코드 처리
    const code = new URLSearchParams(window.location.search).get("code");
    if (code) {
      this.handleKakaoCallback(code);
    }
  },
  methods: {
    handleKakaoLogin() {
      if (
        !process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY ||
        !process.env.VUE_APP_KAKAO_REDIRECT_URI
      ) {
        console.error("Kakao JavaScript Key or Redirect URI is not defined.");
        alert("환경 변수 설정 오류: 관리자에게 문의하세요.");
        return;
      }

      if (!window.Kakao.isInitialized()) {
        try {
          window.Kakao.init(process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY);
          console.log("Kakao SDK initialized successfully.");
        } catch (error) {
          console.error("Kakao SDK initialization failed:", error);
          alert("Kakao SDK 초기화 실패. 다시 시도해주세요.");
          return;
        }
      }

      this.loading = true;

      try {
        console.log("Starting Kakao login process...");
        window.Kakao.Auth.authorize({
          redirectUri: process.env.VUE_APP_KAKAO_REDIRECT_URI,
        });
      } catch (error) {
        console.error("Kakao authorization failed:", error);
        alert("로그인 요청 실패. 다시 시도해주세요.");
        this.loading = false;
      }
    },
    async handleKakaoCallback(code) {
      try {
        const response = await fetch("https://kauth.kakao.com/oauth/token", {
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
          body: new URLSearchParams({
            grant_type: "authorization_code",
            client_id: process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY,
            redirect_uri: process.env.VUE_APP_KAKAO_REDIRECT_URI,
            code,
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();

        if (data.error) {
          throw new Error(data.error_description || "Token error");
        }

        window.Kakao.Auth.setAccessToken(data.access_token);
        console.log("Access Token Set:", data.access_token);

        this.fetchUserInfo();
      } catch (err) {
        console.error("Token Request Error:", err);
        this.errorMessage = "로그인 실패: 다시 시도해주세요.";
        alert(this.errorMessage);
        this.loading = false;
      }
    },
    fetchUserInfo() {
      window.Kakao.API.request({
        url: "/v2/user/me",
        success: (response) => {
          console.log("User Info Response:", response);

          if (
            response.kakao_account &&
            response.kakao_account.profile &&
            response.kakao_account.profile.nickname
          ) {
            this.userName = response.kakao_account.profile.nickname;

            // 로컬 스토리지에 사용자 정보 저장
            localStorage.setItem("kakao_user", JSON.stringify(response));

            alert(`로그인 성공! ${this.userName}님 환영합니다.`);
            this.loading = false;

            // 홈 화면으로 이동
            this.$router.push("/");
          } else {
            console.error("User profile data is not available.");
            this.errorMessage = "사용자 프로필 정보를 가져오지 못했습니다.";
            alert(this.errorMessage);
            this.loading = false;
          }
        },
        fail: (error) => {
          console.error("Failed to fetch user info:", error);
          this.errorMessage = "사용자 정보를 가져오는 데 실패했습니다.";
          alert(this.errorMessage);
          this.loading = false;
        },
      });
    },
    getRandomStarStyle() {
      const randomX = Math.random() * 100; // 0~100% 랜덤 값
      const randomY = Math.random() * 100;
      const randomDelay = Math.random() * 5; // 최대 5초 지연
      const randomDuration = 2 + Math.random() * 3; // 2~5초 지속
      const randomSize = 1 + Math.random() * 2; // 1~3px 크기

      return {
        top: `${randomY}%`,
        left: `${randomX}%`,
        animationDelay: `${randomDelay}s`,
        animationDuration: `${randomDuration}s`,
        width: `${randomSize}px`,
        height: `${randomSize}px`,
      };
    },
  },
};
</script>

<style>
/* 어두운 배경과 별 효과 */
.login-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #000; /* 완전한 어두운 배경 */
  overflow: hidden;
  font-family: Arial, sans-serif;
}

/* 별 필드 */
.star-field {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.star {
  position: absolute;
  width: 3px; /* 별 크기 조정 */
  height: 3px;
  background-color: white; /* 밝은 색상 */
  border-radius: 50%;
  top: calc(100% * var(--random-y, 0.5));
  left: calc(100% * var(--random-x, 0.5));
  animation: twinkle 2s infinite ease-in-out alternate;
}

/* 반짝이는 효과 */
@keyframes twinkle {
  0% {
    opacity: 0.2;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1.4);
  }
}

/* 로그인 박스 스타일 */
.login-box {
  text-align: center;
  background-color: rgba(50, 50, 50, 0.9); /* 더 밝은 반투명 박스 */
  padding: 40px;
  border-radius: 12px;
  border: 2px solid #f7e317; /* 테두리 추가 */
  box-shadow: 0 4px 20px rgba(255, 255, 255, 0.2); /* 밝은 그림자 */
  max-width: 400px;
  width: 100%;
  color: #fff;
  z-index: 1;
}

/* 제목 스타일 */
.login-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #f7e317; /* 카카오 고유 색상 */
}

/* 설명 텍스트 스타일 */
.login-description {
  font-size: 16px;
  color: #ddd;
  margin-bottom: 30px;
}

/* 카카오 버튼 이미지 스타일 */
.login-button {
  cursor: pointer;
  width: 200px;
  height: auto;
  transition: transform 0.2s ease, opacity 0.3s ease;
}

.login-button:hover {
  transform: scale(1.05);
}

.login-button.disabled {
  opacity: 0.5;
  pointer-events: none;
}
</style>
